.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.container{
  padding:50px 10%;
}
table{
  border: 1px solid black;
  border-collapse: collapse;
  width: 100%;
  text-align: center;
}
table tr{
  border-bottom: 1px solid black;
  background-color: rgb(20, 100, 147);
  color: white;
}
table tr th, table tr td{
  padding: 5px;
}
.form-div input {
  width: 80%;
  height: 30px;
  margin-bottom: 10px;
  border-radius: 5px;
  border: 1px thin rgb(100, 98, 98);
}
.form-div form button {
  width: 15%;
  padding: 8px;
  cursor: pointer;
  background-color: rgb(67, 146, 67);
  color: white;
  border-radius: 10px;
  margin-left: 20px;

}